import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import { Tooltip } from '@mui/material';


export default function RemediationButtons({ handleClickSetRemediation, handleClickDisplayRemediation }) {
    return (
        <Stack
            direction={{ xs: 'column', sm: 'row' }}
            spacing={{ xs: 2, sm: 8, md: 12, lg: 24 }}
            m={{ xs: 2, sm: 4, md: 6, lg: 6 }}
            // spacing={24}
            alignItems="center"
            justifyContent="center"
        >
            <Tooltip
                title="Set Remediation for device"
                arrow
                placement='top-end'
            >
                <Button
                    variant="outlined"
                    onClick={handleClickSetRemediation}
                    color="primary"
                    sx={{
                        padding: 2,
                        fontWeight: 'normal',
                        fontSize: 18
                    }}
                >
                    Set Remediation
                </Button>

            </Tooltip>

            <Tooltip
                title="Get all devices which need remediation"
                arrow
                placement='top-end'
            >
                <Button
                    variant="outlined"
                    onClick={handleClickDisplayRemediation}
                    color="primary"
                    sx={{
                        padding: 2,
                        fontWeight: 'normal',
                        fontSize: 18
                    }}
                >
                    Get Remediation
                </Button>
            </Tooltip>
        </Stack >
    )
};
